<template>
  <link href="https://css.gg/search.css" rel="stylesheet" />
  <div class="search__container">
    <i class="gg-search search__container--icon"></i>
    <input
      class="search__container--input"
      type="text"
      :value="value"
      :placeholder="placeholder"
      :required="require"
      :disabled="disable"
      @change="dataUpdated"
    />
  </div>
</template>

<script>
export default {
  name: "SearchBar",

  props: {
    value: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
    require: {
      type: Boolean,
      default: true,
    },
    disable: {
      type: Boolean,
      default: false,
    },
  },

  emits: ["dataUpdate"],

  methods: {
    dataUpdated(event) {
      this.$emit("dataUpdate", event.target.value);
    },
  },
};
</script>

<style>
.search__container {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  width: 52%;
  margin: 0 auto;
}

.search__container--input {
  padding: 3px 3px 3px 30px;
  height: 20px;
  border: none;
}

.search__container--input:focus {
  outline-width: 0;
}

.search__container--input::placeholder {
  color: rgb(206, 206, 206);
  margin-left: 30px;
}

.search__container--icon {
  color: rgba(0, 0, 0, 0.7);
}
</style>
