import axios from "axios";

const api = process.env.VUE_APP_API;

export default async function(location) {
  const params = {
    q: location,
    appid: "c34d0b30de706ed953190741dcd852f2",
    units: "metric",
    lang: "vi",
  };
  try {
    const response = await axios.get(api, { params });
    return response.data;
  } catch (error) {
    throw error;
  }
}
