<template>
  <div class="background-color">
    <ContentLayout>
      <template #left>
        <form @submit.prevent="search">
          <SearchBar
            @dataUpdate="setLocation"
            :value="location"
            placeholder="Enter your location"
            :require="true"
            :disable="isLoading"
          />
        </form>
        <div class="time__container">
          <p class="time__container--day greyColor">{{ date }}</p>
          <h5 class="time__container--hour">{{ time }}</h5>
        </div>
        <h1 v-if="!location" class="location">Hà Nội</h1>
        <h1 v-else class="location">{{ location }}</h1>
        <p class="line"></p>
        <div class="footer">
          <div class="footer--left">
            <div class="sunrise margin-top">
              <p class="greyColor">Sunrise:</p>
              <p class="margin-top-half">{{ sunrise }}</p>
            </div>
            <div class="humid margin-top">
              <p class="greyColor">Humid:</p>
              <p class="margin-top-half">{{ humid }}%</p>
            </div>
          </div>
          <div class="footer--right">
            <div class="sundown margin-top">
              <p class="greyColor">Sundown:</p>
              <p class="margin-top-half">{{ sundown }}</p>
            </div>
            <div class="wind margin-top">
              <p class="greyColor">Wind:</p>
              <p class="margin-top-half">{{ wind }} m/s</p>
            </div>
          </div>
        </div>
      </template>

      <template #right>
        <div class="rightContainer">
          <div class="weather__temperature">
            <WeatherIcon :imgSrc="weatherIcon" />
            <p class="rightContainer--temperature">{{ temperature }}°C</p>
          </div>
          <span class="rightContainer--weatherStatus">{{ weatherStatus }}</span>
        </div>
      </template>
    </ContentLayout>
  </div>
</template>

<script>
import moment from "moment";
import apiService from "./helper/apiService";
import SearchBar from "./components/SearchBar";
import WeatherIcon from "./components/WeatherIcon";
import ContentLayout from "./components/ContentLayout";

export default {
  name: "App",

  components: {
    SearchBar,
    WeatherIcon,
    ContentLayout,
  },

  data() {
    return {
      weatherData: "",
      location: "",
      isLoading: false,
    };
  },

  // default show Hà Nội
  async created() {
    const response = await apiService("Hà Nội");
    this.weatherData = response;
    console.log(response);
  },

  computed: {
    date() {
      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "June",
        "July",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];
      const today = new Date();
      return `${today.getDate()} ${
        months[today.getMonth()]
      } ${today.getFullYear()}`;
    },

    time() {
      const time = new Date();
      if (time.getHours() < 10) {
        var hour = `0${time.getHours()}`;
      } else {
        var hour = time.getHours();
      }
      if (time.getMinutes() < 10) {
        var min = `0${time.getMinutes()}`;
      } else {
        var min = time.getMinutes();
      }
      // var sec = time.getSeconds();
      return `${hour}:${min}`;
    },

    weatherIcon() {
      if (this.weatherData) {
        return this.weatherData.weather[0].icon;
      }
    },

    sunrise() {
      if (this.weatherData) {
        const time = moment.unix(this.weatherData.sys.sunrise).format("H:mm");
        return time;
      }
    },

    sundown() {
      if (this.weatherData) {
        const time = moment.unix(this.weatherData.sys.sunset).format("H:mm");
        return time;
      }
    },

    humid() {
      if (this.weatherData) {
        return this.weatherData.main.humidity;
      }
    },

    wind() {
      if (this.weatherData) {
        return this.weatherData.wind.speed;
      }
    },

    temperature() {
      if (this.weatherData) {
        return this.weatherData.main.temp;
      }
    },

    weatherStatus() {
      if (this.weatherData) {
        return this.weatherData.weather[0].main;
      }
    },
  },

  methods: {
    setLocation(value) {
      if (value) {
        this.location = value;
      }
    },

    async search() {
      try {
        this.isLoading = true;
        const response = await apiService(this.location);
        this.location = response.name;
        this.weatherData = response;
        this.isLoading = false;
        console.log(response);
      } catch (error) {
        alert("Wrong input");
        this.isLoading = false;
        this.location = "- -";
      }
    },
  },
};
</script>
