<template>
  <img :src="iconSrc" :alt="alt" />
</template>

<script>
export default {
  name: "WeatherIcon",

  props: {
    imgSrc: {
      type: String,
      default: "",
    },
    alt: {
      type: String,
      default: "",
    },
  },

  computed: {
    iconSrc() {
      if (this.imgSrc) {
        return `http://openweathermap.org/img/wn/${this.imgSrc}.png`;
      }
    },
  },
};
</script>

<style>
img {
  /* width: 200px; */
}
</style>
